.dr-ed {
  &__container {
    padding: 30px;
    backdrop-filter: blur(7px);
    box-shadow: 0 0 20px 0 rgba(156, 156, 156, 0.1);
    background: rgba(255, 255, 255, 0.8);
    border-radius: 20px;

    @include small-tablet {
      padding: 20px;
      border-radius: 10px;
    }
  }

  &__block {
    &:not(:last-child) {
      margin-bottom: 30px;

      @include small-tablet {
        margin-bottom: 10px;
      }
    }
  }
}
